.Checkout{
    padding: 20px;
    .Checkout__list{
        display: flex;
        flex-direction: column;
        gap: 8px;
    }
}
.Main-Checkout{
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}
.Inputs__List{
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.Checkout__container{
    width: 900px;
    background-color: white;
    box-shadow: .2px .2px 5px grey;
    border-radius: 9px;
    display: flex;
    flex-direction: column;
    padding: 20px 0;

    hr{
        background-color: rgb(75, 74, 74);
    }
    .Title__container,.Input__container{
        padding: 0 10px;
    }
    .Title__container{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .Required{
            color: red;
        }
    }
    .Inputs__container{
        width: 95%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        .Input__container{
            width: calc(100% / 3);
        }
    }

    .Input__container{
        width: 95%;
        display: flex;
        flex-direction: column;

        input{
            width: 100%;
            height: 40px;
            border:1px solid grey;
            background-color: rgba(197, 197, 197, 0.636);
            border-radius: 4px;
            padding-left: 10px;
        }
        input:focus{
            background-color: rgba(197, 197, 197, 0.636);
            outline: 1px solid red;
        }
    }

    .Checkbox__container{
        display: flex;
        flex-direction: row;
        gap: 8px;
        padding: 10px;
        input{
            height: 20px;
            width: 20px;
        }
        input:focus{
            outline: none;
        }
    }
}